import React from 'react';
import { NavLink } from "react-router-dom";

const NavBar = () => {
    return (
    <header>
        <div>
            <nav>
                <NavLink to="/" exact>
                    Wichmann
                </NavLink>
                <NavLink to="about">
                   About Me 
                </NavLink>
                <NavLink to="project">
                  Portfolio
                </NavLink>
                <NavLink to="post" >
                Contact
                </NavLink>
            </nav>
        </div>
    </header>
    )
}

export default NavBar
